import React, { Component } from 'react';
import Header from './HeaderComponent';
import Footer from './FooterComponent';
import Home from './HomeComponent';
import HelpCenter from './HelpCenter';
import {Switch, Route,Redirect, Router} from 'react-router-dom';
import { createBrowserHistory as createHistory } from "history";
const history = createHistory();
const images=
    [
    {name:"images/mcmaster0.jpg", id:1,width:980,height:580},
    {name:"images/mcmaster1.jpg", id:2,width:980,height:580},
    {name:"images/mcmaster2.jpg", id:3,width:600,height:399},
    {name:"images/mcmaster3.jpg", id:4,width:900,height:506},
    {name:"images/mcmaster4.jpg", id:5,width:980,height:580},
    {name:"images/mcmaster5.jpg", id:6,width:1170,height:610},
    ]

class Main extends Component {
  constructor(props){
    super(props);
    this.state={
      imageLib:images,
      imageSelected:{name:"/images/mcmaster0.jpg",id:1,width:980,height:580},
      curId:6
    }
  }
  render() {
    const HomePage=(props)=>{
      return(
        <Home {...props} 
        imageLib={this.state.imageLib} 
        imageSelected={this.state.imageSelected}
        curId={this.state.curId}
        changeImageSelected={(value)=>this.setState({imageSelected:value})} 
        changeImageLib={(value)=>this.setState({imageLib:value})}
        changeCurId={(value)=>this.setState({curId:value})} 
        />
      );
    }
    return (
      <div >
          <Header/>
            <Router basename={'/minigame_index/picsMagic'}>
              <Route path={`${process.env.PUBLIC_URL}/home`} exact component={HomePage} />
              <Route path={`${process.env.PUBLIC_URL}/helpcenter`} component={HelpCenter} />
              <Redirect to={`${process.env.PUBLIC_URL}/home`}></Redirect>
            </Router>
          <Footer />
      </div>
    );
  }
}

export default Main;
